.user-form-button {
  appearance: none;
  box-sizing: border-box;
  color: #000;
  cursor: pointer;
  letter-spacing: 0;
  opacity: 1;
  text-align: center;
  text-rendering: geometricprecision;
  text-transform: uppercase;
  user-select: none;
  touch-action: manipulation;
  vertical-align: baseline;
  white-space: nowrap;
  background-color: #fff;
  border: 1px solid #000;
  outline: 0;
  margin: 0;
  padding: 1em 1.47em;
  font-family: Courier New, Courier, monospace;
  font-size: 14px;
  font-weight: bold;
  line-height: 1em;
  text-decoration: none;
  transition: opacity .3s cubic-bezier(.694, 0, .335, 1), background-color .1s cubic-bezier(.694, 0, .335, 1), color .1s cubic-bezier(.694, 0, .335, 1);
  display: inline-block;
  position: relative;
}

.user-form-button:before {
  backface-visibility: hidden;
  clip-path: polygon(-1% 0, 0 0, -25% 100%, -1% 100%);
  content: "";
  background-color: #6d006db3;
  width: 100%;
  height: 100%;
  transition: clip-path .5s cubic-bezier(.165, .84, .44, 1);
  animation: .5s step-end forwards opacityFallbackOut;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateZ(0);
}

.user-form-button:hover:before {
  clip-path: polygon(0 0, 101% 0, 101% 101%, 0 101%);
  animation: step-start forwards opacityFallbackIn;
}

.user-form-button:after {
  background-color: #fff;
}

.user-form-button span {
  z-index: 1;
  position: relative;
}
